.emails-editor_form {
  background: #FFFFFF;
  border: 1px solid #C3C2CF;
  border-radius: 4px;
  height: 96px;
  overflow-y: auto;
  padding: 8px 7px;
  cursor: text; }

.emails-editor_input {
  min-width: 2px;
  border: none;
  margin-right: 8px; }
  .emails-editor_input:focus {
    outline: none !important; }

.emails-editor_placeholder {
  color: #C3C2CF;
  font-size: 14px;
  line-height: 24px; }
